              </div>
              <div class="u-row" @click="toPage('/correct-info')">
                <p class="u-label">上报地址：</p>
                <p class="u-value">海陵区一城东街道-东苑小区-5#101</p>
                <p class="u-tips">3m</p>
              </div>
              <div class="u-btn-gp">
                <span class="u-btn">详情</span>
