          v-model:loading="state.loading"
          :finished="state.finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <van-cell v-for="item in state.list" :key="item">
            <div class="m-box">
              <div class="u-tp" @click="toPage('/correct-info')">
                <p class="u-name">张磊</p>
                <p class="u-sn">5457454258543</p>
                <van-tag color="#7232dd" plain round size="mini">已完成</van-tag>
