<script lang="ts">
import { defineComponent, ref, reactive } from 'vue'
import router from './../router'

export default defineComponent({
  setup () {
    const list: number[] = []
    const state = reactive({
      list: list,
      loading: false,
      finished: false
    })
    const onLoad = () => {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          state.list.push(state.list.length + 1)
        }

        // 加载状态结束
        state.loading = false

        // 数据全部加载完成
        if (state.list.length >= 40) {
          state.finished = true
        }
      }, 1000)
    }
    const active = ref(2)

    const toPage = (path: string) => router.push(path)
    const toPageReturn = () => router.go(-1)
    return {
      active,
      onLoad,
      state,
      toPage,
      toPageReturn
    }
  }
})
</script>

